@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;500;700&display=swap');

* {
    margin: 0;
    padding: 0;
}
iframe {
    border: 1px solid #ddd;
}
#pizzaa {
    width: 1024px;
    height: 780px;
}
body {
    font-family: 'Noto Sans KR', sans-serif;
}
.container {
    width: 1024px;
    margin: 0 auto;
}

.header {
    width: calc(100% - 20px);
    padding: 10px;
    background-color: #fff;
    color: rgb(75, 159, 138);
    font-size: 20px;
    position: fixed;
    font-weight: 700;
    left: 0;
    top: 0;
    box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.1);
}
.header .container {
    display: flex;
    justify-content: space-between;
}
.nav span {
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-weight: 300;
    border: 1px solid rgb(75, 159, 138);
    color: rgb(75, 159, 138);
    display: inline-block;
    margin-left: 10px;
    cursor: pointer;
    font-size: 15px;
    border-radius: 50%;
}
.nav span:first-child {
    margin-left: 0;
}
.nav span.selected {
    background-color: rgb(75, 159, 138);
    color: #fff;
}
.content {
    padding-top: 80px;
    display: flex;
    justify-content: center;
}
